import {PureComponent} from "react";
import { Modal } from "antd";

import './index.less'

class Index extends PureComponent {

  state = {
    visible: false,
    data: {}
  }

  initModal = (data) => {
    this.setState({
      visible: true,
      data
    })
  }

  closeModal = () => {
    console.log(this.state.data)
    this.setState({
      visible: false
    })
  }

  render() {
    const { visible, data } = this.state
    return(
      <Modal
        title="详情"
        visible={visible}
        className="img-detail-dialog"
        centered
        onCancel={this.closeModal}
        onOk={this.closeModal}>
        <img width="100%" src={data.uri} alt={data.url} />
        <h6 style={{ textAlign: 'center' }}>{data.name}</h6>
        <p>{data.desc}</p>
      </Modal>
    )
  }
}

export default Index;
