import './index.less'
import { Row, Col, Carousel } from 'antd'
import p1 from './images/p1.jpg'
import p2 from './images/p2.jpg'
import p3 from './images/p3.jpg'
import p4 from './images/p4.jpg'
import p5 from './images/p5.jpg'

export default function Main() {
  const imgs = [p1, p2, p3, p4, p5]
  return (
    <div className="main-div">
      <Row gutter={[30, 30]}>
        <Col span={24} lg={12}>
          <h1 className="title">Advocacy-Blockchain Event</h1>
          <p className="content">Hibit has organized two annual Digital Assets Summits (Vancouver) joined by industry experts and leaders in 2018 and 2019 respectively, along with series of seminars and workshops about the development of the blockchain industry and investment opportunities of crypto assets
            We expect to bring more blockchain events to the community post pandemic.</p>
        </Col>
        <Col span={24} lg={12}>
          <Carousel autoplay dots={false} className="carousel">
            {imgs.map((img, index) => (<img src={img} alt="" key={index} />))}
          </Carousel>
        </Col>
      </Row>
    </div>
  )
}
