import './index.less'
import { Row, Col } from 'antd'
// import market1 from './images/market1.jpg'
// import market2 from './images/market2.jpg'
import imageData from './config/data.json'
import {useRef, useState} from "react";

import ImgDetailDialog from "./components/ImgDetailDialog";

const images = [], promises = []
for (let i = 0; i < imageData.images.length; i++) {
  const image = imageData.images[i]
  promises.push(import(`${image.url}`))
  images.push(image)
}

export default function Locations() {
  const dialog = useRef()
  // 图片相关属性和方法
  const [imgs, setImgs] = useState([])
  Promise.allSettled(promises).then(res => {
    for (let i = 0; i < images.length; i++) {
      if (res[i].status === 'fulfilled') {
        images[i].uri = res[i].value.default
      }
    }
    setImgs(images)
  })
  // 图片点击事件
  const imgClick = (data) => {
    return () => {
      const { current: modal } = dialog
      modal.initModal(data)
    }
  }
  return (
    <div className="locations" id="locations">
      {/*<h1>Locations</h1>
      <Row gutter={[30, 30]}>
        <Col lg={12} span={24}>
          <Row className="border-radius">
            <Col sm={12} xs={24} className="left-img">
              <img width="100%" src={market1} alt=""/>
            </Col>
            <Col sm={12} xs={24} className="right-text">
              <h2>Richmond</h2>
              <p>#2230-8260 Westminster Highway, Richmond BC V6X 3Y2 (the second floor of Richmond Public Market)</p>
            </Col>
          </Row>
        </Col>
        <Col lg={12} span={24}>
          <Row className="border-radius">
            <Col sm={12} xs={24} className="left-img">
              <img width="100%" src={market2} alt=""/>
            </Col>
            <Col sm={12} xs={24} className="right-text">
              <h2>Burnaby</h2>
              <p>#2817-4500 Kingsway, Burnaby, BC V5H 2A9 (2nd floor at Crystal Mall)</p>
            </Col>
          </Row>
        </Col>
      </Row>*/}
      <Row gutter={[30, 30]} className="image-list">
        {imgs.map((img, index) => (
          <Col xl={4} lg={6} md={8} span={12} key={index}>
            <img className="img-item" width="100%" src={img.uri} alt={img.name} onClick={imgClick(img)} />
            <p className="img-name">{img.name}</p>
          </Col>
        ))}
      </Row>
      <ImgDetailDialog ref={dialog} />
      <p className="we-are-close">“We are close to you and expanding!”</p>
    </div>
  )
}
