import './index.less'
import { Row, Col, Input, Button } from 'antd'
const { TextArea } = Input

export default function ContactUs() {
  return (
    <div className="contact-us" id="contact">
      <h1>Contact us</h1>
      <p className="edgtf-st-text">
        <strong>Phone: <a href="tel:7788344829">778-834-4829</a> / <a href="tel:6044993201">604-499-3201</a>
        </strong><br />
        <strong>Email:
          <a href="mailto:hello@hibit.pro">hello@hibit.pro</a>
        </strong>
      </p>
      <Row gutter={[30, 10]} style={{ marginBottom: '20px' }}>
        <Col span={24} lg={12}>
          <Input placeholder="Name*" size="large" />
        </Col>
        <Col span={24} lg={12}>
          <Input placeholder="Email*" size="large" />
        </Col>
        <Col span={24} className="textarea-container">
          <TextArea placeholder="How might we best assist you?" style={{ height: '140px' }} />
        </Col>
      </Row>
      <Button type="primary" shape="round" size="large">
        <strong>SUBMIT</strong>
      </Button>
    </div>
  )
}
