import './index.less'
import logo from './images/hibi-new-logo-light.png'
import logo1 from './images/logo-black.png'
import { useState } from 'react'

import { MenuOutlined } from '@ant-design/icons'

export default function Index() {
  const [show, setShow] = useState(false)
  const iconClick = () => {
    setShow(!show)
  }
  return (
    <div className="header">
      <div className="header-big">
        <ul className="header-big-right">
          <li><a href="#locations">LOCATIONS</a></li>
          <li><a href="#contact">CONTACT US</a></li>
          <li><a href="#test">ENGLISH</a></li>
        </ul>
        <div className="header-big-left">
          <img src={logo} alt="" height="33" />
        </div>
      </div>
      <div className="header-sm">
        <div className="header-sm-left">
          <MenuOutlined onClick={iconClick} />
        </div>
        <div className="header-sm-center">
          <img src={logo1} alt="" height="33" />
        </div>
        <ul className={ show ? 'header-sm-right show' : 'header-sm-right'}>
          <li><a href="#locations">Locations</a></li>
          <li className="has-top-border"><a href="#contact">Contact Us</a></li>
        </ul>
      </div>
    </div>
  )
}
